import sourceNodesAndRemoveStaleNodes from "../utils/source-nodes";
import reporter from "../reporter";
import { assertStore } from "../utils/assert-store";
import { IDataLayerContext } from "./types";

export async function sourceNodes({
  parentSpan,
  webhookBody,
  webhookSourcePluginName,
  store,
  deferNodeMutation = false,
}: Partial<IDataLayerContext>): Promise<void> {
  assertStore(store);

  const activity = reporter.activityTimer(`Fetching API Data`, {
    parentSpan,
  });
  activity.start();
  await sourceNodesAndRemoveStaleNodes({
    parentSpan: activity.span,
    deferNodeMutation,
    webhookBody,
    pluginName: webhookSourcePluginName,
  });
  activity.end();
}
